/*
 * Rajdhani
 * ======================================================================== */
@font-face {
  font-family: 'Rajdhani';
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/Rejdhani-Regular.woff") format("woff"), url("../fonts/Rejdhani-Regular.woff2") format("woff2"); }

/*
 * Sohne
 * ======================================================================== */
@font-face {
  font-family: 'Sohne';
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/Sohne-Mager.woff") format("woff"), url("../fonts/Sohne-Mager.woff2") format("woff2"); }

@font-face {
  font-family: 'Sohne';
  font-style: normal;
  font-weight: bold;
  src: url("../fonts/Sohne-Fett.woff") format("woff"), url("../fonts/Sohne-Fett.woff2") format("woff2"); }

/*
 * Base
 * ======================================================================== */
body {
  background: #FFF;
  color: #000;
  font-family: "Sohne", sans-serif;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-weight: normal;
  height: 100%; }

/*
 * Type
 * ======================================================================== */
a,
a:active,
a:visited {
  color: #000;
  text-decoration: underline;
  transition: color 100ms linear; }

a:hover {
  color: rgba(0, 0, 0, 0.6); }

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0; }

/*
 * Layout
 * ======================================================================== */
main {
  flex: 1 0 auto;
  z-index: 1; }

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }
